@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600&display=swap);
body {
  font-size: 16px;
  line-height: 1.5;
  overflow-x: hidden;
}
*:not(i) {
  font-family: "Raleway", sans-serif;
  font-weight: 400;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}
:before,
:after {
  box-sizing: border-box;
}
a {
  text-decoration: none;
}


.home-section .full-screen {
  padding: 50px 0;
  min-height: 100vh;
  -webkit-align-items: center;
          align-items: center;
}
.home-section {
  position: relative;
}

@media (max-width: 750px) {
  .home-section .full-screen {
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
    padding: 0;
    min-height: 80vh;
  }
  .home-section .full-screen .home-text {
    margin-top: 30px;
  }
  .home-section .full-screen .home-text .title {
    width: 280px !important;
    margin: auto;
    font-size: 22px;
  }
}
.home-section .home-text,
.home-section .home-img {
  -webkit-flex: 0 0 50%;
          flex: 0 0 50%;
  max-width: 50%;
  padding: 0 15px;
}

.home-section .home-text p {
  color: cyan;
  font-weight: 400;
  font-size: 18px;
  text-transform: capitalize;
  margin: 0;
}
.home-section .home-text h2 {
  font-size: 55px;
  font-weight: 700;
  color: cyan;
}
.home-section .home-text h1 {
  font-weight: 20px;
  font-weight: 500;
  color: cyan;
  margin: 0;
  text-transform: capitalize;
}
.home-section .home-img .img-box {
  max-width: 375px;
  display: block;
  margin: auto;
  border-radius: 50%;
  padding: 15px;
}

.home-section .home-img .img-box img {
  /* width: 90%; */
  width:98%;
  border-radius: 50%;
  margin-left: 5px;
}

.home-section .home-img .img-box {
  box-shadow: 0 0 25px !important;
}
.shadow { /* Safari 3-4, iOS 4.0.2 - 4.2, Android 2.3+ */ /* Firefox 3.5 - 3.6 */
  box-shadow: 12px 8px 9px 8px #fb839e; /* Opera 10.5, IE 9, Firefox 4+, Chrome 6+, iOS 5 */
  border-radius: 50%;
}

.about-me-btn {
  box-shadow: 0 0 10px;
  text-decoration: none;
  font-size: 18px;
  font-weight: 500;
  width: 175px;
  padding: 4px;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 30px;
  text-decoration: none;
}
.title a {
  text-decoration: none;
}

.about-section {
  padding: 80px 0 80px;
  min-height: 100vh;
}
.section-title {
  padding: 0 15px;
  -webkit-flex: 0 0 100%;
          flex: 0 0 100%;
  max-width: 100%;
  text-align: center;
  margin-bottom: 80px;
}

.section-title h2 {
  display: inline-block;
  font-size: 30px;
  font-weight: 700;
  color: #000000;
  text-transform: uppercase;
  margin: 0;
}
.section-titile h2:before {
  content: attr(data-heading);
  display: block;
}
.img-box {
  max-width:580px;
  display: block;
  margin: auto;
  /* padding: 15px; */
  box-shadow: 0 0 1px;
}

.about-section .about-img {
  -webkit-flex: 0 0 40%;
          flex: 0 0 40%;
  max-width: 40%;
  padding: 0 1px;
}
.about-section .about-img .img-box {
  padding: 3px;
  border-radius: 5px;
}
.about-section .about-img .img-box img {
  width: 100%;
  border: 10px solid transparent;
  border-radius: 5px;
}
.about-section .about-info {
  -webkit-flex: 0 0 60%;
          flex: 0 0 60%;
  padding: 0 15px;
  max-width: 60%;
}

.about-section .about-info h6 {
  font-weight: 700;
  font-size: 22px;
}
.about-section .about-info p {
  font-weight: 18px;
  line-height: 26px;
  margin-bottom: 15px;
  color: #373636;
}
.about-section .about-info a {
  text-decoration: none;
}
.about-btn {
  box-shadow: 0 0 15px;
  text-decoration: none;
  font-size: 18px;
  font-weight: 500;
  width: 175px;
  padding: 6px;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 30px;
  text-decoration: none;
  margin: 10px;
}

.about-links {
  display: -webkit-flex;
  display: flex;
  margin: 20px;
}
.about-links-icons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.about-desc {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.about-desc-span {
  margin-left: 5px;
  font-weight: 400;
  font-size: 18px;
}
.social-icons {
  display: -webkit-flex;
  display: flex;
  border: 1px solid black;
  width: 50%;
  margin: auto;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
}

@media (max-width: 750px) {
  .about-section {
    margin-top: 120px;
  }
  .row {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .img-box {
    display: none;
  }
  .about-info {
    max-width: 100% !important;
  }
  .about-desc {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .about-desc-span {
    margin-left: 5px;
    font-size: 10px;
  }
  .about-btn {
    font-size: 12px;
    font-weight: 500;
    width: 235px;
    padding: 3px;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    border-radius: 30px;
    text-decoration: none;
    margin: 10px;
  }
}

